.animation-wrapper {
    height: 20vh;
    display: grid;
    place-items: center;
    /* margin: -25px; */
  }
  
  .animation {
    width: 18ch;
    animation: typing 3s steps(18), blink 0.5s step-end infinite alternate;
    white-space: nowrap;
    overflow: hidden;
    border-right: 3px solid;
    font-family: monospace;
    font-size: 4em;
  }
  
  @keyframes typing {
    from {
      width: 0
    }
  }
      
  @keyframes blink {
    50% {
      border-color: transparent
    }
  }



  @media screen and (max-width: 968px) {
    .animation {
      font-size: 2.5em;
    }
  }

  @media screen and (max-width: 425px) {
    .animation {
      font-size: 1.5em;
    }
  }